import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Alert, Card, CardBody,CardTitle } from 'reactstrap';
import MoneyButtonDonate from "./components/MoneyButtonDonate.jsx";

class App extends Component {

  state = {
    devMode: true,
    type: "buy",
    to:"145",
    labelMoneyButton: "Slide to Donate",
    labelAmount: "Enter Amount",
    labelReference: "Order Number"
  }

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  updateType = val => event => {
    if (event.target) {
      if (event.target.checked) {
        // this.props.updateCategory(e.target.value)
        this.setState({
          type: val,
        });
      }
    }
  }

  styles = {
    padding: '3px'
  };

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to money-button-donate</h1>
        </header>
        <div style={{"textAlign":"left", "margin":"20px"}}>
          <p>View source on GitHub <a href="https://github.com/dfoderick/money-button-donate">https://github.com/dfoderick/money-button-donate</a></p>
          <div>
            <Alert color="primary">
              Money Button is working in demo mode. That means no funds will be spent from your account.
            </Alert>
            {/* <div>
              <input type="radio" checked={this.state.devMode === true}></input> Dev
              <input type="radio" checked={this.state.devMode === false}></input> Live
            </div> */}
            <Card>
            <CardBody>
              <CardTitle>Customize your MoneyButton</CardTitle>
            <div class="col-md-12 form-group form-inline">
            <div class="col-md-12 form-group" style={{...this.styles}}>
                <label class="col-sm-2 col-form-label"  for="name">Type</label>
                <input type="radio" radioGroup="type" class="form-control" checked={this.state.type === 'tip'} onChange={this.updateType('tip')}></input> Tip
                <input type="radio" radioGroup="type" class="form-control" checked={this.state.type === 'buy'} onChange={this.updateType('buy')}></input> Buy
              </div>
              <div class="col-md-12 form-group" style={{...this.styles}}>
                <label class="col-sm-2 col-form-label"  for="name">Send to (userid or address)</label>
                 <input type="text" class="form-control" value={this.state.to} onChange={this.handleChange("to")}></input>
              </div>
              <div class="col-md-12 form-group" style={{...this.styles}}>
                <label class="col-sm-2 col-form-label"  for="name">Money Button Label</label>
                 <input type="text" class="form-control" value={this.state.labelMoneyButton} onChange={this.handleChange("labelMoneyButton")}></input>
              </div>
              <div class="col-md-12 form-group" style={{...this.styles}}>
                <label class="col-sm-2 col-form-label"  for="name">Prompt for Amount</label>
                 <input type="text" class="form-control" value={this.state.labelAmount} onChange={this.handleChange("labelAmount")}></input>
              </div>
              <div class="col-md-12 form-group" style={{...this.styles}}>
              <label class="col-sm-2 col-form-label"  for="name">Prompt for Reference</label>
                 <input type="text" class="form-control" value={this.state.labelReference} onChange={this.handleChange("labelReference")}></input>
              </div>
              </div>
              </CardBody>
            </Card>          
           </div>
           <p></p>

          <Card>
            <CardBody>
            <CardTitle>Select amount with a textbox...</CardTitle>
              <MoneyButtonDonate display="input" buttonId="input" 
                devMode={this.state.DevMode} labelMoneyButton={this.state.labelMoneyButton}
                labelAmount = {this.state.labelAmount} labelReference = {this.state.labelReference}
                type={this.state.type}
              />
              </CardBody>
          </Card>

          <Card>
            <CardBody>
            <CardTitle>Select amount with a slider control...</CardTitle>
              <MoneyButtonDonate buttonId="slider" 
                devMode={this.state.DevMode} labelMoneyButton={this.state.labelMoneyButton}
                labelAmount = {this.state.labelAmount} labelReference = {this.state.labelReference}
                type={this.state.type}
              />
              </CardBody>
          </Card>

          <Card>
            <CardBody>
            <CardTitle>Select amount with a drop down control...</CardTitle>
              <MoneyButtonDonate display="dropdown" buttonId="dropdown" 
                devMode={this.state.DevMode} labelMoneyButton={this.state.labelMoneyButton}
                labelAmount = {this.state.labelAmount} labelReference = {this.state.labelReference}
                type={this.state.type}
              />
              </CardBody>
          </Card>
        </div>
      </div>
    );
  }
}

export default App;



